import React from 'react'
import { Link, useParams } from 'react-router-dom'
import styled from 'styled-components'
import colors from '../../utils/style/colors'
import { Loader } from '../../utils/style/Atoms'
import { useDispatch, useSelector } from 'react-redux'
import { selectAnswers, selectTheme } from '../../utils/selectors'
import { saveAnswer } from '../../features/answers'
import { useQuery } from 'react-query'

const SurveyContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`

const QuestionTitle = styled.h2`
  text-decoration: underline;
  text-decoration-color: ${colors.primary};
  color: ${({ theme }) => (theme === 'light' ? '#000000' : '#ffffff')};
`

const QuestionContent = styled.span`
  margin: 30px;
  color: ${({ theme }) => (theme === 'light' ? '#000000' : '#ffffff')};
`

const LinkWrapper = styled.div`
  padding-top: 30px;

  & a {
    color: ${({ theme }) => (theme === 'light' ? '#000000' : '#ffffff')};
  }

  & a:first-of-type {
    margin-right: 20px;
  }
`

const ReplyBox = styled.button`
  border: none;
  height: 100px;
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: ${({ theme }) =>
          theme === 'light' ? colors.backgroundLight : colors.backgroundDark};
  color: ${({ theme }) => (theme === 'light' ? '#000000' : '#ffffff')};
  border-radius: 30px;
  cursor: pointer;
  box-shadow: ${(props) =>
          props.isSelected ? `0px 0px 0px 2px ${colors.primary} inset` : 'none'};

  &:first-child {
    margin-right: 15px;
  }

  &:last-of-type {
    margin-left: 15px;
  }
`

const ReplyWrapper = styled.div`
  display: flex;
  flex-direction: row;
`

function Survey() {
  const { questionNumber } = useParams()
  const questionNumberInt = parseInt(questionNumber)
  const prevQuestionNumber = questionNumberInt === 1 ? 1 : questionNumberInt - 1
  const nextQuestionNumber = questionNumberInt + 1
  const theme = useSelector(selectTheme)
  const answers = useSelector(selectAnswers)
  const dispatch = useDispatch()

  function saveReply(answer) {
    dispatch(saveAnswer({ questionNumber, answer }))
  }

  const { data, isLoading, error } = useQuery('survey', async () => {
    const response = await fetch('https://shiny.hermanneho.com/survey')
    const data = await response.json()
    return data
  })

  if (error != null) {
    return <span>Oups il y a eu un problème</span>
  }

  return (
    <SurveyContainer>
      <QuestionTitle theme={theme}>Question {questionNumber}</QuestionTitle>
      {isLoading ? (
        <Loader data-testid='loader' />
      ) : (
        <>
          <QuestionContent theme={theme} data-testid='question-content'>
            {data.surveyData && data.surveyData[questionNumber]}
          </QuestionContent>
          <ReplyWrapper>
            <ReplyBox
              onClick={() => saveReply(true)}
              isSelected={answers[questionNumber] === true}
              theme={theme}
            >
              Oui
            </ReplyBox>
            <ReplyBox
              onClick={() => saveReply(false)}
              isSelected={answers[questionNumber] === false}
              theme={theme}
            >
              Non
            </ReplyBox>
          </ReplyWrapper>
          <LinkWrapper theme={theme}>
            <Link to={`/survey/${prevQuestionNumber}`}>Précédent</Link>
            {data.surveyData && data.surveyData[questionNumberInt + 1] ? (
              <Link to={`/survey/${nextQuestionNumber}`}>Suivant</Link>
            ) : (
              <Link to='/results'>Résultats</Link>
            )}
          </LinkWrapper>
        </>
      )}
    </SurveyContainer>
  )
}

export default Survey
